.q-dashboard-img{
    margin: 22px 0 30px 0;
    border:1px solid #ccc;
    box-shadow: 13px 15px 18px -8px rgb(0 0 0 / 23%);
}

@media (max-width:720px) {
    .q-dashboard-img{
        margin-bottom: 80px;
    }
}
a[data-fancybox]::after {
    content: "";
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    background: url(https://ik.imagekit.io/qualys/image/zoom-icon.svg) center center no-repeat rgb(255 255 255 / 65%);
    background-size: 16px;
    border-radius: 3px;
    top:20px;
    right: 6px;
}
.q-fancybox-link{
    position: relative;
}